* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.container {
  width: 100vw;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  max-width: 700px;
  width: 100%;
  margin: 0 auto;

  h1 {
    font-size: clamp(1.5rem, 5vw, 3.5rem);
    text-transform: capitalize;
    text-align: center;
    margin: 10vh 0 5vh;
    @media screen and (max-width: 600px) {
      margin: 2rem 0;
    }
  }
}
